import { Pipe, PipeTransform } from '@angular/core';
import { IUsers } from '../models/users.model';

@Pipe({
  name: 'filterUsers',
})
export class FilterUsersPipe implements PipeTransform {
  transform(arrUsers: any[], value: string, arrComplete: any[]): any {
    if (value === '') {
      return arrUsers;
    }
    const text = value.toLocaleLowerCase();

    return arrComplete.filter((user: IUsers) => {
      const country = user.country.toLowerCase();
      const province = user.province.toLowerCase();
      const city = user.city.toLowerCase();
      const full_name = user.full_name.toLowerCase();
      return (
        full_name.includes(text) ||
        country.includes(text) ||
        province.includes(text) ||
        city.includes(text)
      );
    });
  }
}
