import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { IUsers } from 'src/app/models/users.model';
import { SubjectsService } from 'src/app/services/subjects';
import { ActionsPage } from '../../actions/actions.page';

@Component({
  selector: 'app-players',
  templateUrl: './players.page.html',
  styleUrls: ['./players.page.scss'],
})
export class PlayersPage implements OnInit {
  public filterTime = 'cero';
  public filterDay = 'todos';
  public textSearch = '';
  public fullUsers: IUsers[] = [];
  public showUsers: IUsers[] = [];

  public days = [
    'todos',
    'lunes',
    'martes',
    'miercoles',
    'jueves',
    'viernes',
    'sabado',
    'domingo',
  ];

  constructor(
    private subjects: SubjectsService,
    private popCtrl: PopoverController
  ) {
    this.subjects.users$.subscribe((data) => {
      this.fullUsers = [...data];
      this.showUsers = data;
    });
  }

  ngOnInit() {}

  searchUsers(event: any) {
    this.textSearch = event.detail.value;
  }

  openMail(num) {
    var win = window.open('mailto:' + num, '_blank');
    win.focus();
  }
  openWhatspp(num) {
    var win = window.open(
      'https://api.whatsapp.com/send?phone=+549' + num,
      '_blank'
    );
    win.focus();
  }
  openCall(num) {
    var win = window.open('tel:' + num, '_blank');
    win.focus();
  }

  async Opciones(ev: any, item: any) {
    const popover = await this.popCtrl.create({
      component: ActionsPage,
      cssClass: 'my-custom-class',
      translucent: true,
      event: ev,
      mode: 'ios',
      componentProps: {
        cellphone: item.cellphone,
        expose_phone: item.expose_phone,
        expose_email: item.expose_email,
        email: item.email,
      },
    });
    await popover.present();

    const { role } = await popover.onDidDismiss();
  }

  Filtrar() {
    console.log(this.fullUsers);
    console.log(this.filterDay);
    console.log(this.filterTime);
    this.showUsers = this.fullUsers.filter((user) => {
      return user.schedule.some((schedule) => {
        const retulst =
          (this.filterDay === 'todos' || schedule.day === this.filterDay) &&
          (this.filterTime === 'cero' || schedule.times[this.filterTime]);
        return retulst;
      });
    });
    console.log(this.showUsers);
  }
}
